// frontend/src/components/AddBedForm.jsx

import React, { useState } from 'react';
import axios from 'axios';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const AddBedForm = ({ onBedAdded, specialties }) => {
    const [formData, setFormData] = useState({
        ward_name: '',
        specialty_type: 'General',
        current_status: 'AVAILABLE'
    });
    const [isSubmitting, setIsSubmitting] = useState(false);
    const token = localStorage.getItem('authToken');

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsSubmitting(true);
        try {
            await axios.post(`${API_BASE_URL}/beds`, formData, {
                headers: { Authorization: `Bearer ${token}` }
            });
            alert("New bed registered successfully!");
            setFormData({ ...formData, ward_name: '' }); // Reset ward name
            onBedAdded(); // Refresh the dashboard list
        } catch (err) {
            alert("Error creating bed.");
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="bg-white p-6 rounded-xl shadow-md border border-indigo-100">
            <h2 className="text-xl font-bold mb-4 flex items-center">
                <span className="mr-2">‚ûï</span> Register New Bed
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                    <label className="block text-xs font-bold text-gray-500 uppercase">Ward Name</label>
                    <input 
                        type="text" 
                        required
                        className="w-full border p-2 rounded mt-1 focus:ring-2 focus:ring-indigo-500 outline-none"
                        placeholder="e.g. Ward B, Level 4"
                        value={formData.ward_name}
                        onChange={(e) => setFormData({...formData, ward_name: e.target.value})}
                    />
                </div>
                <div>
                    <label className="block text-xs font-bold text-gray-500 uppercase">Specialty Type</label>
                    <select 
                        className="w-full border p-2 rounded mt-1 focus:ring-2 focus:ring-indigo-500 outline-none"
                        value={formData.specialty_type}
                        onChange={(e) => setFormData({...formData, specialty_type: e.target.value})}
                    >
                        {specialties.filter(s => s !== 'All').map(spec => (
                            <option key={spec} value={spec}>{spec}</option>
                        ))}
                    </select>
                </div>
                <button 
                    type="submit" 
                    disabled={isSubmitting}
                    className="w-full bg-indigo-600 text-white font-bold py-2 rounded hover:bg-indigo-700 transition-colors disabled:bg-gray-400"
                >
                    {isSubmitting ? 'Registering...' : 'Add Bed to Inventory'}
                </button>
            </form>
        </div>
    );
};

export default AddBedForm;