// frontend/src/components/BedCard.jsx

import React, { useState, useEffect } from 'react';
import axios from 'axios';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const BedCard = ({ bedData, onStatusChange, token }) => {
    const [status, setStatus] = useState(bedData.current_status);
    const bedId = bedData.bed_id;

    useEffect(() => {
        setStatus(bedData.current_status);
    }, [bedData.current_status]);
    
    const getStatusColor = (currentStatus) => {
        switch (currentStatus) {
            case 'AVAILABLE': return 'bg-green-100 text-green-800 border-green-300';
            case 'OCCUPIED': return 'bg-red-100 text-red-800 border-red-300';
            case 'CLEANING': return 'bg-yellow-100 text-yellow-800 border-yellow-300';
            default: return 'bg-gray-100 text-gray-800 border-gray-300';
        }
    };

    const handleStatusUpdate = async (newStatus) => {
        let patientName = null;

        // If admitting, ask for a name
        if (newStatus === 'OCCUPIED') {
            patientName = window.prompt("Enter Patient Name for Admission:");
            if (!patientName) return; 
        }

        const actionText = newStatus === 'CLEANING' ? 'DISCHARGE patient' : `set bed to ${newStatus}`;
        if (!window.confirm(`Are you sure you want to ${actionText}?`)) return;

        try {
            await axios.post(`${API_BASE_URL}/beds/${bedId}/status`, 
                { 
                    new_status: newStatus,
                    patient_name: patientName 
                },
                { headers: { 'Authorization': `Bearer ${token}` } }
            );
            
            setStatus(newStatus); 
            onStatusChange(); 
        } catch (error) {
            console.error(error);
            alert('Failed to update status.');
        }
    };
    
    return (
        <div className={`p-4 border-2 rounded-lg shadow-sm transition-all flex flex-col justify-between ${getStatusColor(status)}`}>
            <div>
                <div className="flex justify-between items-start">
                    <h3 className="font-bold text-lg">Bed {bedId}</h3>
                    <span className="text-[10px] uppercase font-bold px-2 py-0.5 rounded-full border border-current">
                        {status}
                    </span>
                </div>
                
                <div className="mt-2 text-sm space-y-1">
                    <p><span className="font-semibold">Ward:</span> {bedData.ward_name}</p>
                    <p><span className="font-semibold">Specialty:</span> {bedData.specialty_type}</p>
                </div>

                {status === 'OCCUPIED' && (
                    <div className="mt-4 p-3 bg-white bg-opacity-60 rounded-md border border-red-200">
                        <p className="text-[10px] uppercase font-bold text-red-500 mb-1">Current Patient</p>
                        <p className="text-base font-bold text-red-900">
                            üë§ {bedData.patient_name || 'Anonymous'}
                        </p>
                    </div>
                )}
            </div>
            
            <div className="mt-4 flex flex-col gap-2">
                {status === 'OCCUPIED' && (
                    <button className="bg-red-600 text-white font-bold py-2 rounded-md text-xs hover:bg-red-700"
                        onClick={() => handleStatusUpdate('CLEANING')}>Discharge Patient</button>
                )}
                {status === 'CLEANING' && (
                    <button className="bg-yellow-600 text-white font-bold py-2 rounded-md text-xs hover:bg-yellow-700"
                        onClick={() => handleStatusUpdate('AVAILABLE')}>Mark Clean</button>
                )}
                {status === 'AVAILABLE' && (
                    <button className="bg-green-600 text-white font-bold py-2 rounded-md text-xs hover:bg-green-700"
                        onClick={() => handleStatusUpdate('OCCUPIED')}>Direct Admission</button>
                )}
            </div>
        </div>
    );
};

export default BedCard;